import React, { useState, useEffect, useRef } from 'react';
import './Mobile.css';
import { db } from './firebase';
import {
  collection, addDoc, serverTimestamp, onSnapshot, query, orderBy, doc, getDoc, setDoc
} from 'firebase/firestore';
import jsPDF from 'jspdf';
import autoTable from 'jspdf-autotable';
import { useSwipeable } from 'react-swipeable';

function App() {
  // State variables
  const [drinkList, setDrinkList] = useState([]);
  const [cart, setCart] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState('All');
  const [activeTab, setActiveTab] = useState('drinks');
  const [tableNumber, setTableNumber] = useState('');
  const [orders, setOrders] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [lastOrderTimestamp, setLastOrderTimestamp] = useState(null);
  const [adminPasswordInput, setAdminPasswordInput] = useState('');
  const [authenticated, setAuthenticated] = useState(false);
  const [showAdminLogin, setShowAdminLogin] = useState(false);
  const [isVisible, setIsVisible] = useState(true);
  const [visibilityLoading, setVisibilityLoading] = useState(true);
  const [showWelcome, setShowWelcome] = useState(true);

  // NEW STATE for QR Payment Integration
  const [showPaymentModal, setShowPaymentModal] = useState(false);
  const [paymentConfirmed, setPaymentConfirmed] = useState(false);
  const [paymentQRUrl, setPaymentQRUrl] = useState('');
  const [adminQRInput, setAdminQRInput] = useState('');
  const [qrLoading, setQrLoading] = useState(true);

  const audioRef = useRef(new Audio('/alert.mp3'));
  const adminPassword = '1234';

  const tabOrder = ['drinks', 'cart', 'admin'];

  // Swipe handlers for tab navigation
  const swipeHandlers = useSwipeable({
    onSwipedLeft: () => {
      const index = tabOrder.indexOf(activeTab);
      setActiveTab(tabOrder[(index + 1) % tabOrder.length]);
    },
    onSwipedRight: () => {
      const index = tabOrder.indexOf(activeTab);
      setActiveTab(tabOrder[(index - 1 + tabOrder.length) % tabOrder.length]);
    },
    delta: 10,
    trackMouse: true,
  });

  // Load drinks list from Google Apps Script endpoint
  useEffect(() => {
    fetch('https://script.google.com/macros/s/AKfycbwIFfJUcYsdQpq_0LHebNkwl7-rfLRhyVOVld25rkwa6_J6FeU8yoPzfFfX_EVS27pB6Q/exec')
      .then(res => res.json())
      .then(data => {
        const formatted = data.filter(item => item && item.name).map((item, index) => ({
          id: item.id || index.toString(),
          name: String(item.name || ''),
          description: item.description || '',
          image: item.image || '',
          price: parseFloat(item.price) || 0,
          category: item.category || 'Uncategorized',
        }));
        setDrinkList(formatted);
      })
      .catch(() => alert('Failed to load drinks. Please check your data source.'));
  }, []);

  // Unlock audio on first user interaction (some browsers require this)
  useEffect(() => {
    const unlockAudio = () => {
      audioRef.current.play().catch(() => {});
      document.removeEventListener('click', unlockAudio);
    };
    document.addEventListener('click', unlockAudio);
    return () => document.removeEventListener('click', unlockAudio);
  }, []);

  // Load visibility setting from Firestore
  useEffect(() => {
    const fetchVisibility = async () => {
      try {
        const docRef = doc(db, 'settings', 'visibility');
        const docSnap = await getDoc(docRef);
        if (docSnap.exists()) {
          setIsVisible(docSnap.data().isVisible);
        }
      } catch (error) {
        console.error('Error checking visibility:', error);
      }
      setVisibilityLoading(false);
    };
    fetchVisibility();
  }, []);

  // Subscribe to live orders and play alert audio on new order
  useEffect(() => {
    const q = query(collection(db, 'orders'), orderBy('timestamp', 'desc'));
    const unsubscribe = onSnapshot(q, snapshot => {
      const liveOrders = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      setOrders(prev => {
        const latestOrder = liveOrders[0];
        if (latestOrder?.timestamp?.seconds !== lastOrderTimestamp) {
          setLastOrderTimestamp(latestOrder?.timestamp?.seconds);
          if (document.visibilityState === 'visible') {
            audioRef.current.play().catch(() => {});
          }
        }
        return liveOrders;
      });
    });
    return () => unsubscribe();
  }, [lastOrderTimestamp]);

  // Load payment QR code URL from Firestore settings collection
  useEffect(() => {
    const fetchPaymentQR = async () => {
      try {
        const qrDocRef = doc(db, 'settings', 'paymentQR');
        const qrDocSnap = await getDoc(qrDocRef);
        if (qrDocSnap.exists()) {
          setPaymentQRUrl(qrDocSnap.data().url || '');
          setAdminQRInput(qrDocSnap.data().url || '');
        }
      } catch (err) {
        console.error('Failed to fetch payment QR code URL:', err);
      }
      setQrLoading(false);
    };
    fetchPaymentQR();
  }, []);

  // Add drink to cart or update quantity if already present
  const addToCart = (drink) => {
    setCart(prevCart => {
      const existing = prevCart.find(item => item.id === drink.id);
      if (existing) {
        return prevCart.map(item =>
          item.id === drink.id ? { ...item, quantity: item.quantity + 1 } : item
        );
      } else {
        return [...prevCart, { ...drink, quantity: 1 }];
      }
    });
  };

  // Update quantity of a cart item
  const updateQuantity = (id, amount) => {
    setCart(prev =>
      prev.map(item =>
        item.id === id ? { ...item, quantity: Math.max(1, item.quantity + amount) } : item
      )
    );
  };

  // Remove item from cart
  const removeItem = (id) => {
    setCart(prev => prev.filter(item => item.id !== id));
  };

  // Calculate total price of cart
  const getTotal = () => cart.reduce((sum, item) => sum + item.price * item.quantity, 0);

  // Total quantity of all items in cart
  const totalItemsInCart = cart.reduce((sum, item) => sum + item.quantity, 0);

  // Extract unique categories for filtering
  const categories = ['All', ...Array.from(new Set(drinkList.map(d => d.category)))];

  // Filter drinks by selected category
  const filteredDrinks = selectedCategory === 'All'
    ? drinkList
    : drinkList.filter(d => d.category === selectedCategory);

  // Filter orders for today's date and search term
  const filteredOrders = orders.filter(order => {
    const today = new Date();
    const orderDate = order.timestamp?.toDate?.();
    const isToday = orderDate?.toDateString() === today.toDateString();
    const str = `${order.table} ${order.drink} ${order.price}`.toLowerCase();
    return isToday && str.includes(searchTerm.toLowerCase());
  });

  // Calculate total revenue today
  const totalRevenue = filteredOrders.reduce((sum, order) => sum + (order.total || 0), 0);

  // Admin login function
  const loginAdmin = () => {
    if (adminPasswordInput === adminPassword) {
      setAuthenticated(true);
      setShowAdminLogin(false);
      setActiveTab('admin');
    } else {
      alert('Incorrect password');
    }
  };

  // Admin logout function
  const logoutAdmin = () => {
    setAuthenticated(false);
    setShowAdminLogin(false);
    setAdminPasswordInput('');
    setActiveTab('drinks');
  };

  // Submit order with payment flow: show payment modal first
  const handleSubmitOrder = () => {
    if (!tableNumber) return alert('Please select table number');
    if (cart.length === 0) return alert('Cart is empty');
    setShowPaymentModal(true);
  };

  // After user confirms payment is done, submit order data to Firestore
  const confirmPaymentAndSubmit = async () => {
    try {
      const batch = cart.map(item => addDoc(collection(db, 'orders'), {
        timestamp: serverTimestamp(),
        table: tableNumber,
        drink: item.name,
        quantity: item.quantity,
        price: item.price,
        total: item.price * item.quantity,
      }));
      await Promise.all(batch);
      setCart([]);
      setTableNumber('');
      setPaymentConfirmed(true);
      setShowPaymentModal(false);
      alert('Order submitted successfully!');
    } catch (error) {
      alert('Failed to submit order. Please try again.');
      console.error(error);
    }
  };

  // Toggle app visibility
  const toggleVisibility = async () => {
    try {
      const docRef = doc(db, 'settings', 'visibility');
      await setDoc(docRef, { isVisible: !isVisible });
      setIsVisible(!isVisible);
    } catch (err) {
      console.error('Failed to toggle visibility:', err);
    }
  };

  // Export orders as CSV file
  const exportToCSV = () => {
    const rows = [['Time', 'Table', 'Drink', 'Qty', 'Total']];
    filteredOrders.forEach(order => {
      const date = order.timestamp?.toDate?.().toLocaleString?.() || '';
      rows.push([date, order.table, order.drink, order.quantity, order.total]);
    });
    const csv = rows.map(r => r.join(',')).join('\n');
    const blob = new Blob([csv], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'orders.csv';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  // Export orders as PDF file
  const exportToPDF = () => {
    const doc = new jsPDF({ orientation: 'portrait', unit: 'pt', format: 'A4' });
    doc.setFontSize(18);
    doc.text('BLACKOUT BAR - Orders', 40, 40);

    const rows = filteredOrders.map(order => [
      order.timestamp?.toDate?.().toLocaleString?.() || '',
      order.table || '',
      order.drink || '',
      order.quantity || 0,
      `¥${order.total || 0}`,
    ]);

    if (rows.length === 0) return alert('No orders to export.');

    autoTable(doc, {
      startY: 60,
      head: [['Time', 'Table', 'Drink', 'Qty', 'Total']],
      body: rows,
      theme: 'grid',
      styles: { fontSize: 10 },
      headStyles: { fillColor: [0, 0, 0] },
    });

    doc.save('blackout_orders.pdf');
  };

  // Save updated payment QR URL from admin input to Firestore
  const savePaymentQRUrl = async () => {
    if (!adminQRInput) {
      alert('QR code URL cannot be empty.');
      return;
    }
    try {
      const qrDocRef = doc(db, 'settings', 'paymentQR');
      await setDoc(qrDocRef, { url: adminQRInput });
      setPaymentQRUrl(adminQRInput);
      alert('Payment QR code updated successfully!');
    } catch (err) {
      console.error('Failed to save payment QR code URL:', err);
      alert('Failed to update payment QR code.');
    }
  };

  // Loading & offline screens
  if (visibilityLoading || qrLoading) return <p>Loading...</p>;
  if (!isVisible && !authenticated) return <h1>BLACKOUT BAR is currently offline.</h1>;

  // Welcome screen shown on initial load
  if (showWelcome) {
    return (
      <div className="App welcome-screen">
        <h1 className="app-title">WELCOME TO BLACKOUT BAR</h1>
        <p style={{ textAlign: 'center', margin: '2rem 1rem' }}>
          Explore our finest selection of cocktails, spirits, and more. Slide to browse, add to cart, and place your order directly from your table.
        </p>
        <div style={{ textAlign: 'center' }}>
          <button onClick={() => setShowWelcome(false)}>Enter Bar</button>
        </div>
      </div>
    );
  }

  return (
    <div className="App" {...swipeHandlers}>
      <h1 className="app-title">BLACKOUT BAR</h1>

      {/* Drinks tab */}
      {activeTab === 'drinks' && (
        <>
          <div className="category-buttons-container">
            {categories.map(cat => (
              <button
                key={cat}
                className={`category-button ${selectedCategory === cat ? 'active' : ''}`}
                onClick={() => setSelectedCategory(cat)}
              >
                {cat}
              </button>
            ))}
          </div>
          <div className="drink-grid">
            {filteredDrinks.map(drink => (
              <div className="drink-card" key={drink.id}>
                <img src={drink.image} alt={drink.name} className="drink-img" />
                <h3>{drink.name}</h3>
                <p>{drink.description}</p>
                <p>¥{drink.price}</p>
                <button onClick={() => addToCart(drink)}>Add to Cart</button>
              </div>
            ))}
          </div>
        </>
      )}

      {/* Cart tab */}
      {activeTab === 'cart' && (
        <div>
          <h2>Your Cart</h2>
          {cart.length === 0 ? (
            <p>Your cart is empty</p>
          ) : (
            <ul className="cart-list">
              {cart.map(item => (
                <li key={item.id} className="cart-item">
                  <img src={item.image} alt={item.name} className="cart-img" />
                  <div className="cart-details">
                    <strong>{item.name}</strong><br />
                    ¥{item.price} × {item.quantity} = ¥{item.price * item.quantity}
                    <div className="cart-buttons">
                      <button onClick={() => updateQuantity(item.id, -1)}>➖</button>
                      <button onClick={() => updateQuantity(item.id, 1)}>➕</button>
                      <button onClick={() => removeItem(item.id)}>🗑️</button>
                    </div>
                  </div>
                </li>
              ))}
            </ul>
          )}
          <h3>Total: ¥{getTotal()}</h3>

          {cart.length > 0 && (
            <div className="checkout-form">
              <h3>Confirm Table Number</h3>
              <select value={tableNumber} onChange={(e) => setTableNumber(e.target.value)}>
                <option value="">Select Table</option>
                {Array.from({ length: 100 }, (_, i) => (
                  <option key={i + 1} value={i + 1}>Table {i + 1}</option>
                ))}
              </select>
              <button onClick={handleSubmitOrder}>Submit Order</button>
            </div>
          )}
        </div>
      )}

      {/* Payment Modal */}
      {showPaymentModal && (
        <div className="modal-overlay" onClick={() => setShowPaymentModal(false)}>
          <div className="modal-content" onClick={e => e.stopPropagation()}>
            <h3>Please complete your payment</h3>
            <p>Scan this QR code to pay:</p>
            {paymentQRUrl ? (
              <img src={paymentQRUrl} alt="Payment QR Code" style={{ maxWidth: '100%', height: 'auto' }} />
            ) : (
              <p>No payment QR code available.</p>
            )}
            <button onClick={confirmPaymentAndSubmit}>I Have Paid</button>
            <button onClick={() => setShowPaymentModal(false)}>Cancel</button>
          </div>
        </div>
      )}

      {/* Admin tab */}
      {authenticated && activeTab === 'admin' && (
        <div className="admin-panel">
          <h2>Admin Dashboard</h2>
          <input
            type="text"
            placeholder="Search today's orders..."
            value={searchTerm}
            onChange={e => setSearchTerm(e.target.value)}
          />
          <p>Total Revenue (Today): ¥{totalRevenue}</p>
          <button onClick={toggleVisibility}>{isVisible ? 'Hide Bar' : 'Show Bar'}</button>
          <button onClick={exportToPDF}>Export PDF</button>
          <button onClick={exportToCSV}>Export CSV</button>

          {/* Admin Payment QR Code Update Section */}
          <div style={{ marginTop: '2rem', borderTop: '1px solid #ccc', paddingTop: '1rem' }}>
            <h3>Update Payment QR Code URL</h3>
            <input
              type="text"
              placeholder="Enter new payment QR code URL"
              value={adminQRInput}
              onChange={e => setAdminQRInput(e.target.value)}
              style={{ width: '100%', marginBottom: '0.5rem' }}
            />
            <button onClick={savePaymentQRUrl}>Save QR Code URL</button>
            {paymentQRUrl && (
              <div style={{ marginTop: '1rem' }}>
                <p>Current Payment QR Code Preview:</p>
                <img src={paymentQRUrl} alt="Current Payment QR" style={{ maxWidth: '200px', height: 'auto' }} />
              </div>
            )}
          </div>

          <button onClick={logoutAdmin} style={{ marginTop: '2rem' }}>Exit Admin</button>

          <table className="admin-orders-table">
            <thead>
              <tr>
                <th>Time</th>
                <th>Table</th>
                <th>Drink</th>
                <th>Quantity</th>
                <th>Total (¥)</th>
              </tr>
            </thead>
            <tbody>
              {filteredOrders.map(order => (
                <tr key={order.id}>
                  <td>{order.timestamp?.toDate?.().toLocaleString?.() || ''}</td>
                  <td>{order.table}</td>
                  <td>{order.drink}</td>
                  <td>{order.quantity}</td>
                  <td>{order.total}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      {/* Bottom navigation */}
      <div className="bottom-nav">
        <button className={activeTab === 'drinks' ? 'active' : ''} onClick={() => setActiveTab('drinks')}>Drinks</button>
        <button className={activeTab === 'cart' ? 'active' : ''} onClick={() => setActiveTab('cart')}>
          Cart {totalItemsInCart > 0 && <span className="cart-badge">{totalItemsInCart}</span>}
        </button>
        <button
          className={activeTab === 'admin' ? 'active' : ''}
          onClick={() => {
            setShowAdminLogin(true);
            setActiveTab('admin');
          }}
        >
          Admin
        </button>
      </div>

      {/* Admin login modal */}
      {!authenticated && showAdminLogin && activeTab === 'admin' && (
        <div className="admin-login">
          <h3>Enter Admin Password</h3>
          <input
            type="password"
            value={adminPasswordInput}
            onChange={e => setAdminPasswordInput(e.target.value)}
            placeholder="Password"
          />
          <button onClick={loginAdmin}>Login</button>
        </div>
      )}
    </div>
  );
}

export default App;
